/* -------------------------------------------------------------------------- 
	Variables
  -------------------------------------------------------------------------- */

  :root {

    // Quick view button.
    --addonify_qv_button_text: rgba(255, 255, 255, 1);
    --addonify_qv_button_text_hover: rgba(255, 255, 255, 1);
    --addonify_qv_button_background: rgb(33, 33, 33);
    --addonify_qv_button_background_hover: rgb(14, 86, 255);
    --addonify_qv_button_border_style: solid;
    --addonify_qv_button_border_width: 0;
    --addonify_qv_button_border_color: rgba(255, 255, 255, 0);
    --addonify_qv_button_border_color_hover: rgba(255, 255, 255, 0);
    --addonify_qv_button_border_radius: 4px;


    // Modal
    --addonify_qv_modal_overlay_background: rgba(0, 0, 0, 0.6);
    --addonify_qv_modal_background: rgba(255, 255, 255, 1);
    --addonify_qv_modal_general_text_color: rgba(51, 51, 51, 1);
    --addonify_qv_modal_general_border_color: #E5E5E5; // NEW - Added
    --addonify_qv_modal_general_text_font_size: 15px; // NEW - Added

    --addonify_qv_modal_inputs_background_color: rgba(255, 255, 255, 1);
    --addonify_qv_modal_inputs_text_color: rgba(51, 51, 51, 1);

    --addonify_qv_modal_width: calc( 100vw - 30% ); // PLANNED WITH RESPONSIVE CONTROL. 
    --addonify_qv_modal_content_column_gap: 40px; // NEW - Added
    
    --addonify_qv_modal_border_radius: 10px; // NEW - Added
    --addonify_qv_modal_zindex: 10000000000000000; // NEW - Added 

    // Product
    --addonify_qv_product_title: rgba(51, 51, 51, 1);
    --addonify_qv_product_excerpt: rgba(88, 88, 88, 1);
    --addonify_qv_product_rating_filled: rgba(245, 196, 14, 1);
    --addonify_qv_product_rating_empty: rgba(185, 185, 185, 1);
    --addonify_qv_product_price: rgba(51, 51, 51, 1);
    --addonify_qv_product_price_sale: rgba(255, 0, 0, 1);
    --addonify_qv_product_meta: rgba(2, 134, 231, 1);
    --addonify_qv_product_meta_hover: rgba(88, 88, 88, 1);
    --addonify_qv_stock_in: rgba(0, 128, 0, 1);
    --addonify_qv_stock_out: rgba(255, 0, 0, 1);
    --addonify_qv_product_onsale_badge_text: rgba(255, 255, 255, 1);
    --addonify_qv_product_onsale_badge_background: rgba(255, 0, 0, 1);

    --adonify_qv_product_title_font_size: 32px; // NEW - Added
    --adonify_qv_product_title_font_weight: normal; // NEW - Added
    --adonify_qv_product_title_line_height: 1.2; // NEW - Added
    --adonify_qv_product_price_font_size: 22px; // NEW - Added
    --adonify_qv_product_price_font_weight: normal; // NEW - Added
    --addonify_qv_product_onsale_badge_font_size: 14px; // NEW - Added

    // Image gallery
    --addonify_qv_gallery_trigger_icon_color: #9F9F9F; // NEW - Added
    --addonify_qv_gallery_trigger_icon_color_hover: rgb(54, 91, 255); // NEW - Added
    --addonify_qv_gallery_trigger_icon_background_color: #FFFFFF; // NEW - Added
    --addonify_qv_gallery_trigger_icon_background_color_hover: #FFFFFF; // NEW - Added
    --addonify_qv_modal_images_border_color: rgba(238, 238, 238, 1); // NEW - Added
    --addonify_qv_gallery_trigger_icon_size: 18px; // NEW - Added
    --addonify_qv_gallery_trigger_icon_border_radius: 5px; // NEW - Added
    --addonify_qv_modal_image_border_radius: 10px; // NEW - Added 
    --addonify_qv_modal_gallery_thumb_in_row: 4; // NEW - Done
    --addonify_qv_modal_gallery_thumbs_gap: 20px; // NEW - Done
    
    // Close button
    --addonify_qv_close_button_text: rgba(118, 118, 118, 1);
    --addonify_qv_close_button_text_hover: rgba(2, 134, 231, 1);
    --addonify_qv_close_button_background: rgba(238, 238, 238, 1);
    --addonify_qv_close_button_background_hover: rgba(182, 222, 255, 1);
    --addonify_qv_mobile_close_button_font_size: 14px;

    // Misc buttons
    --addonify_qv_misc_button_text: rgba(255, 255, 255, 1);
    --addonify_qv_misc_button_text_hover: rgba(255, 255, 255, 1);
    --addonify_qv_misc_button_background: rgba(51, 51, 51, 1);
    --addonify_qv_misc_button_background_hover: rgba(2, 134, 231, 1);
    --addonify_qv_misc_button_height: 50px; // NEW - Done
    --addonify_qv_misc_button_border_radius: 5px; // NEW - Done
    --addonify_qv_misc_button_font_size: 15px; // NEW - Added
    --addonify_qv_misc_button_font_weight: normal; // NEW - Added
    --addonify_qv_misc_button_letter_spacing: 0; // NEW - Done
    --addonify_qv_misc_button_line_height: 1; // NEW - Done
    --addonify_qv_misc_button_text_transform: capitalize; // NEW - Added

    // Spinner 
    --addonify_qv_modal_spinner_icon_color: rgba(51, 51, 51, 1);
    --addonify_qv_spinner_icon_size: 28px; // NEW - Added
  }